import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
  },
  {
    path: "/",
    name: "Home",
    component: () => import("../views/Home.vue"),
    children: [
      {
        path: "/produtos",
        name: "Produtos",
        component: () =>
          import("../views/Cadastros/Produtos/ProdutosLista.vue"),
      },
      {
        path: "/usuarios",
        name: "Usuarios",
        component: () =>
          import("../views/Cadastros/Usuarios/UsuariosLista.vue"),
      },
      {
        path: "/colaboradores",
        name: "Colaboradores",
        component: () =>
          import("../views/Cadastros/Colaboradores/ColaboradoresLista.vue"),
      },
      {
        path: "/filiais",
        name: "Filiais",
        component: () => import("../views/Cadastros/Filial/FilialLista.vue"),
      },
      {
        path: "/times",
        name: "Times",
        component: () => import("../views/Cadastros/Times/TimesLista.vue"),
      },
      {
        path: "/centrocustos",
        name: "CentroCustos",
        component: () =>
          import("../views/Cadastros/CentroCusto/CentroCustoLista.vue"),
      },
      {
        path: "/solicitacoes",
        name: "Solicitacoes",
        component: () => import("../views/Solicitacao/SolicitacaoLista.vue"),
      },
      {
        path: "/solicitacao",
        name: "Solicitacao",
        component: () => import("../views/Solicitacao/SolicitacaoForm.vue"),
      },
      {
        path: "/solicitacao/:id",
        name: "SolicitacaoEdit",
        component: () => import("../views/Solicitacao/SolicitacaoForm.vue"),
      },
      {
        path: "/solicitacaoAprovar",
        name: "SolicitacaoAprovar",
        component: () =>
          import("../views/SolicitacaoAprovacao/SolicitacaoAprovacaoLista.vue"),
      },
      {
        path: "/solicitacaoAprovar/:id",
        name: "SolicitacaoAprovarItens",
        component: () =>
          import(
            "../views/SolicitacaoAprovacao/SolicitacaoAprovacaoItensLista.vue"
          ),
      },
      {
        path: "/solicitacoesEmAprovacao",
        name: "SolicitacoesEmAprovacao",
        component: () =>
          import("../views/Solicitacao/TimeLinesSolicitacaoLista.vue"),
      },
      {
        path: "/solicitacoesEmAprovacao/:id",
        name: "SolicitacoesEmAprovacaoTimeLine",
        component: () =>
          import("../views/Solicitacao/TimeLinesSolicitacao.vue"),
      },
      {
        path: "/solicitacoesNaoIntegradas/",
        name: "SolicitacoesNaoIntegradas",
        component: () =>
          import("../views/Solicitacao/SolicitacoesNaoIntegradas.vue"),
      },
      {
        path: "/userServiceLayer",
        name: "UserServiceLayer",
        component: () =>
          import("../views/Portal_Configuration/UserServiceLayer.vue"),
      },
      {
        path: "/emailConfiguration",
        name: "EmailConfiguration",
        component: () =>
          import("../views/Portal_Configuration/EmailConfiguration.vue"),
      },
      {
        path: "/grupoItens",
        name: "GrupoItem",
        component: () =>
          import("../views/Cadastros/GrupoItem/GrupoItensLista.vue"),
      },
      {
        path: "/alterarSenha",
        name: "ChangePass",
        component: () => import("../views/Perfil/AlterarSenha.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: "/portalcompras/",
  // base: "/",
  routes,
});

export default router;
