export class Storage {

    static valKey = "kodigos_compras_sap_";

    static salvar(key: string, data: string) {
        localStorage.setItem(this.valKey + key, data);
        //sessionStorage.setItem("kodigos_compras_sap_login", "true");
    }

    static get(key: string): string | null {
        return localStorage.getItem(this.valKey + key)
    }

}